import { upyunUrl } from '../../api/config'

export default {
  data () {
    return {
      upyunUrl,
      textareaVal: '',
      sex: 0,
      minNum: 0,
      maxNum: 0,
      text: '',
      checkState: 0,
      initialStatus: false
    }
  },
  methods: {
    // 关闭弹窗
    onShowStatusFn () {
      this.$emit('popupStatusChild', false, this.textareaVal)
    },
    // 保存填写内容
    onSaveFn () {
      if (this.textareaVal) {
        this.$emit('saveInfo', false, this.type, this.textareaVal)
      } else {
        this.showToastFn('不能提交空内容!')
        return false
      }
    },
    // 清空数据
    onEmptyFn () {
      this.textareaVal = ''
    },
    // 单选勾选
    onCheckStatusFn (index) {
      this.sex = index
      this.textareaVal = index
    },
    showToastFn (title) {
      uni.showToast({
        title,
        icon: 'none',
        duration: 1500
      })
    }
  },
  props: {
    oJson: {
      type: String,
      default: ''
    },
    type: {
      type: String,
      default: ''
    },
    showState: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    oJson (str) {
      if (str) {
        this.textareaVal = str
      } else {
        this.onEmptyFn()
      }
    },
    type (type) {
      if (type === 'nickname') {
        this.minNum = 1
        this.maxNum = 10
        this.text = '最多不能超过10个字'
      } else if (type === 'user_desc') {
        this.minNum = 10
        this.maxNum = 30
        this.text = '请输入不少于10个字的介绍'
      } else if (type === 'plan') {
        this.minNum = 10
        this.maxNum = 300
        this.text = '最多不能超过300个字'
      } else if (type === 'sex') {
        this.sex = parseInt(this.oJson)
      }
    }
  }
}
